@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.landing_container {
    width: 100%;
    padding: 0 !important;
    overflow: hidden;
}

.section {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

.first_section {
    background-image: url(/static/media/landing_bg_1.a715a9a0.svg);
    background-size: cover;
}

.first_section .details{
    /*height: 100%;*/
    /*max-height: 80vh;*/
}

.first_section .title {
    font-family: 'Playfair Display', serif;
    color: white;
    font-weight: bold;
    font-size: 68px;
}

.first_section .text {
    font-family: 'Montserrat', sans-serif;
    font-style: italic;
    font-weight: 300;
    font-size: 55px;
    /*color: #FCDFB3;*/
    color: white;
}

.first_section .text span{
    /*background-color: #FCDFB3;*/
    /*padding: 12px;*/
    /*border-radius: 30px;*/
    /*text-shadow: 2px 2px 10px saddlebrown;*/
}


.first_section .button, .first_section .button:active, .first_section .button:visited {
    font-weight: bold;
    font-size: 28px;
    color: #201D19;
    border-radius: 46px;
    background-color: rgba(186, 27, 27, 1) !important;
    border: rgba(186, 27, 27, 1) !important;
    outline: none !important;
}

.first_section .button div {
    padding: 16px 50px;
}

.img{
    width: 100%;
    max-width: 830px;
    max-height: 464px;
}

.second_section .title, .third_section .title{
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    font-size: 55px;
    color: black;
    margin-bottom: 30px;
}

.second_section .text, .third_section .text {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 30px;
    color: #4F3B24;
    margin-bottom: 50px;
}

.second_section .button, .second_section .button:active, .second_section .button:hover, .second_section .button:visited,
.third_section .button, .third_section .button:active, .third_section .button:hover, .third_section .button:visited{
    background-color: white !important;
    outline: none !important;
    border: 2px solid #BA1B1B !important;
    font-weight: bold;
    color: #BA1B1B;
    padding: 15px 40px;
    font-size: 28px;
    border-radius: 46px;
}

.second_section .button:active, .second_section .button:visited, .third_section .button:active, .third_section .button:visited{
    background-color: #BA1B1B !important;
    transition: 0.2s;
}

.fourth_section{
   background: rgba(196, 196, 196, 0.19);
}

.fourth_section .logo{
    max-height: 180px;
    max-width: 180px;
}

.fourth_section .text div, .fourth_section .text div:hover, .fourth_section .text div:active,
.fourth_section .text div:visited{
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 25px;
    color: black !important;
    text-decoration: none !important;
}

.fourth_section .text .data{
    color: #BA1B1B;
}

@media screen and (min-width: 1601px){
    .first_section .title {
        font-size: 90px;
        margin-bottom: 40px;
    }
    .first_section .text {
        font-size: 70px;
        margin-bottom: 60px;
    }
    .first_section .button, .first_section .button:active, .first_section .button:visited {
        font-size: 35px;
    }
    .first_section .button div {
        padding: 20px 45px;
    }
}
@media screen and (min-width: 1024px) and (max-width: 1599px){
    .first_section .title {
        font-size: 45px;
        margin-bottom: 40px;
    }
    .first_section .text {
        font-size: 30px;
        margin-bottom: 50px;
    }
}
@media screen and (min-width: 1024px) and (max-width: 1365px) and (max-height: 835px){
    .first_section .title {
        font-size: 50px;
        margin-bottom: 40px;
    }
    .first_section .text {
        font-size: 40px;
        margin-bottom: 40px;
    }
    .first_section .button, .first_section .button:active, .first_section .button:visited {
        font-size: 20px;
    }
    .first_section .button div {
        padding: 10px 25px;
    }

    .second_section .title, .third_section .title{
        font-size: 35px;
    }
    .second_section .text, .third_section .text{
        font-size: 25px;
    }
    .second_section .button, .second_section .button:active, .second_section .button:hover,
    .second_section .button:visited, .third_section .button, .third_section .button:active,
    .third_section .button:hover, .third_section .button:visited{
        padding: 15px 35px;
        font-size: 24px;
        border-radius: 36px;
    }
}
@media screen and (min-width: 1024px) {
    .first_section .details {
        margin-right: 20px;
    }
}
@media screen and (max-width: 1023px) {
    .landing_container {
        text-align: center;
    }
    .first_section .details {
        max-width: 760px;
    }
    .first_section .title {
        margin-bottom: 65px;
    }
    .first_section .text {
        margin-bottom: 145px;
    }
    .second_section .title, .third_section .title{
        margin-top: 40px;
        margin-bottom: 25px;
    }
    .second_section .text, .third_section .text{
        margin-bottom: 40px;
    }
}
@media screen and (max-width: 414px){
    .first_section .title {
        font-size: 38px;
        margin-bottom: 45px;
    }
    .first_section .text {
        font-size: 28px;
        margin-bottom: 110px;
    }
    .first_section .button, .first_section .button:active, .first_section .button:visited {
        font-size: 15px;
    }
    .first_section .button div {
        padding: 7px 20px;
    }
    .second_section .title, .third_section .title{
        font-size: 25px;
    }
    .second_section .text, .third_section .text{
        font-size: 20px;
    }
    .second_section .button, .second_section .button:active, .second_section .button:hover,
    .second_section .button:visited, .third_section .button, .third_section .button:active,
    .third_section .button:hover, .third_section .button:visited{
        padding: 10px 20px;
        font-size: 14px;
        border-radius: 26px;
    }
    .fourth_section .text div, .fourth_section .text div:hover, .fourth_section .text div:active,
    .fourth_section .text div:visited{
        font-size: 15px;
    }
}
@media screen and (max-width: 320px) {
    .first_section .title {
        margin-bottom: 35px;
    }
    .first_section .text {
        margin-bottom: 35px;
    }
}

.main_page{
    background-image: url(/static/media/main_page_bg.92ad36c8.svg);
    background-size: cover;
    height: 100vh;
}

.title{
    margin-top: 50px;
    margin-bottom: 25px;
    font-size: 35px;
    text-align: center;
    color: indianred;
}

.area{
    max-width: 100%;
    width: 100%;
    margin-bottom: 50px;
    border-radius: 10px;

    background: linear-gradient(180deg, #FFFFFF 26.09%, rgba(255, 255, 255, 0) 100%);
    border: 2px solid #803939;
    -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
            filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.main_page .button{
    color: black !important;
    background-color: #FCC608 !important;
    border: 2px solid #FCC608 !important;
    border-radius: 47px !important;
    padding: 10px 30px !important;
    font-weight: 600 !important;
    font-size: 20px !important;
    margin-bottom: 20px;
}

.main_page .button:hover, .main_page .button:active{
    transition: 0.3s;
    color: #FCC608 !important;
    background-color: white !important;
}

.main_page .button:active{
    transition: 0s;
    background-color: ghostwhite !important;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.result{
    font-size: 25px;
    color: darkred;
    font-weight: 500;
}
